.form {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  width: 200px;
  padding: 8px;
  font-size: 20px;
}
.button {
  margin: 12px;
  height: 30px;
  width: 130px;
  font-size: 16px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.button:hover {
  color: rgb(49, 49, 216);
}
